const StudentDetails = props => {
  return (
    <section>
      <h2>student detail page</h2>
      <table id="details-table">
        <thead>
          <tr>
            <th>first name</th>
            <th>last name</th>
            <th>birth</th>
            <th>unique id</th>
            <th>password</th>
          </tr>
        </thead>
        <tbody>
          {props.student.map(st => (
            <tr key={st.id} id={st.id}>
              <td>
                <span>{st.fName} </span>
              </td>
              <td>
                <span>{st.lName} </span>
              </td>
              <td>{st.year}</td>
              <td>{st.id}</td>
              <td>{st.password}</td>
            </tr>
          ))}
        </tbody>
      </table>
    </section>
  );
};

export default StudentDetails;
